.about__container {
  box-shadow: 0px 5px 10px 5px rgba(0, 0, 0, 0.25);
  width: 100%;
  max-width: 400px;
  padding: 20px;
  margin: 0 auto;
  margin-top: 100px;
  background-color: #fafafa;
  border-radius: 8px;
  font-weight: 300;
}

.about__container span:first-child {
  font-size: 20px;
}

.about__container p {
  margin-bottom: 0;
}
