.nav {
  z-index: 1;
  top: 50px;
  left: 10px;
  position: fixed;
  display: flex;
  flex-direction: column;
  padding: 10px 20px;
  background-color: #fafafa;
  box-shadow: 0px 5px 10px 5px rgba(50, 50, 93, 0.25),
    0px 10px 10px 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.nav a {
  text-decoration: none;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 500;
  text-align: center;
  color: #0008fc;
}

.nav a:not(:last-child) {
  margin-bottom: 20px;
}

@media screen and (max-width: 1090px) {
  .nav {
    top: initial;
    left: initial;
    bottom: 0;
    width: 100%;
  }
}
