.container {
  width: 100%;
  padding-bottom: 30px;
}

.detail {
  width: 500px;
  margin: 0 autol;
  margin-top: 20px;
  box-shadow: 0px 5px 10px 5px rgba(50, 50, 93, 0.25),
    0px 12px 10px 12px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.detail__img {
  display: block;
  margin: 0 auto;
  margin-top: 40px;
  text-align: center;
}

.detail__title {
  font-weight: 500;
  text-align: center;
}

.detail__title span:first-child {
  font-size: 25px;
  margin-right: 15px;
}

.detail__info {
  color: #444;
  text-align: center;
}

.detail__info span:first-child {
  margin-right: 40px;
}

.detail__summary {
  font-size: 18px;
  padding-bottom: 20px;
  border-bottom: 1px solid #ddd;
  color: #222;
  font-weight: 300;
  margin: 0px 40px;
}

.detail__genres {
  list-style: none;
  padding-left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.detail__genres > li {
  margin-right: 20px;
  color: #444;
}
